import React from 'react';
import classes from './ProfileInfo.module.css';
import preloader from './../../common/Preloader.js';
import ProfileStatus from './ProfileStatus.jsx';
import ProfileStatusWithHooks from './ProfileStatusWithHooks.jsx';
import imageUser from './../../../assets/images/user.png';
import { Formik, Form, Field, ErrorMessage, useFormikContext } from 'formik';



const ProfileInfoDataForm = ({profile}) => {

    const setProfileInfo = (values) => {
        fetch(`https://social-network.samuraijs.com/api/1.0/profile`, {
         method: 'PUT',
         headers: {
             "Content-Type": "application/json",
             "API-KEY": "7ed1a18b-da95-467c-8ff8-12eb66b13f4e"
         },
         credentials: 'include',
         body: JSON.stringify({
             userId: profile.userId,
             fullName: values.fullName,
             lookingForAJob: values.lookingForAJob,
             lookingForAJobDescription: values.lookingForAJobDescription,
             aboutMe: values.aboutMe,
             contacts: {
                github: 'https://www.facebook.com/alexander.kusmenko.5/',
                vk: 'https://www.facebook.com/alexander.kusmenko.5/',
                facebook: 'https://www.facebook.com/alexander.kusmenko.5/',
                instagram: 'https://www.facebook.com/alexander.kusmenko.5/',
                twitter: 'https://www.facebook.com/alexander.kusmenko.5/',
                website: 'https://www.facebook.com/alexander.kusmenko.5/',
                youtube: 'https://www.facebook.com/alexander.kusmenko.5/',
                mainLink: 'https://www.facebook.com/alexander.kusmenko.5/'
             }
         }) 
     })
        .then(response => response.json)
        .then(json => console.log(json))
    }

    return   <Formik
    initialValues={{fullName: profile.fullName,
        lookingForAJob: profile.lookingForAJob,
        lookingForAJobDescription: profile.lookingForAJobDescription,
        aboutMe: profile.aboutMe,
        facebook: profile.contacts.facebook,
        website: profile.contacts.website,
        vk: profile.contacts.vk,
        twitter: profile.contacts.twitter,
        instagram: profile.contacts.instagram,
        youtube: profile.contacts.youtube,
        githab: profile.contacts.githab,
        mainLink: profile.contacts.mainLink}}

        validate={values => {
           const errors = {};
           return errors;
         }}

        onSubmit={(values, {setSubmitting}) => {
            setProfileInfo(values);
            setSubmitting(false);
            }}
        >
            {({isSubmitting}) => (
                <Form>
                    <div>
                        <b>Full name: </b> <Field name="fullName" placeholder="Your full name"/>
                    </div>

                    <div>
                        <b>lookong for a job: </b> <Field type="checkbox" name="lookingForAJob"/>
                    </div>
           

                    <div>
                        <b>My professional skills: </b> <Field name="lookingForAJobDescription" placeholder="You professional skills"/>
                    </div>


                    <div>
                        <b>About me: </b> <Field name="aboutMe" placeholder="Tell about yourself"/>
                    </div>

                    <div><b>Contacts: </b></div>

                    <div>
                        <b>facebook: </b> <Field name="facebook" placeholder="Facebook"/>
                    </div>

                    <div>
                        <button type="submit" disabled={isSubmitting}>
                           Submit
                        </button>
                    </div>
                </Form>
                )}
    </Formik>

}

const Contact = ({contactTitle, contactValue}) => {
    return <div><b>{contactTitle}</b>: {contactValue}</div>
}


export default ProfileInfoDataForm